.nav-link::after {
  content: "";
  margin-top: 2.5px;
  display: flex;
  border-bottom: solid 1px #8e0103;
  transform: scaleX(0);
  transition: transform 0.25s ease-in-out;
}

.active::after {
  transform: scaleY(3);
}

p {
