  line-height: 1.85;
}

#navA {
  cursor: grabbing;
}

#navAHome,
#navAAbout,
#navASkill,
#navAProject,
#navAContact {
  width: 105px;
  text-decoration: none;
  color: white;
  cursor: pointer;
  transition: all 0.5s ease;
}

.navBarButton {
  border: none !important;
}
.navBarButton:focus {
  box-shadow: none !important;
}

.fixed-top {
  transition: all 0.5s ease;
}

#navbarNavAltMarkup {
  transition: all 0.5s ease;
}

.bg-lg-cover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.bg-attachment-fixed {
  background-attachment: fixed;
}

.object-fit-cover {
  -o-object-fit: cover;
     object-fit: cover;
